.sidebar {
  flex: 3;
  margin: 20px;
  padding-bottom: 30px;
  background-color: #fbfdfb;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.sidebarItem {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.sidebarTitle {
  margin: 10px;
  padding: 5px;
  width: 80%;
  border-top: 1px solid #a7a4a4;
  border-bottom: 1px solid #a7a4a4;
  font-family: "Varela Round", sans-serif;
  font-size: 12px;
  color: #222;
  font-weight: 600;
  line-height: 20px;
  text-align: center;
}

.sidebarItem > img {
  margin-top: 15px;
  width: 85%;
}

.sidebarItem > p {
  padding: 10px 30px;
}

.sidebarList {
  list-style: none;
  margin-bottom: 30px;
}

.sidebarListItem {
  display: inline-block;
  width: 50%;
  margin-top: 15px;
  cursor: pointer;
}

.sidebarSocial {
  margin-top: 15px;
  width: 250px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.sidebarIcon {
  font-size: 16px;
  margin-left: 10px;
  cursor: pointer;
}

.category > .category-list {
  font-family: monospace;
  padding: 0 1rem;
}

.category > .category-list .list-items {
  background: skyblue;
  margin: 0.8rem 0;
  border-radius: 3rem;
  width: 70%;
  display: flex;
  justify-content: space-between;
}

.category > .category-list .list-items a {
  color: #000;
  padding: 0.2rem 0.5rem;
}

.sidebar .popular-farm .foodTitle {
  padding-top: 10px;
  padding-bottom: 10px;
}

.sidebar .popular-post .post-content {
  padding: 1rem 0;
}

.sidebar .popular-post h2 {
  padding-top: 8rem;
}

.sidebar .popular-post .post-info {
  padding: 0.4rem 0.1rem !important;
  bottom: 0rem !important;
  left: 1.5rem !important;
  border-radius: 0rem !important;
  background: var(--white) !important;
}
.sidebar .popular-post .post-title a {
  font-size: 1rem;
}

 .sidebar .sidebarItem >  img{
    width: 100%;
    -webkit-transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
     transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
 }
 
  .sidebar .sidebarItem > img:hover{
    transform: scale(1.1);
    transition: all 1s ease;
 }

 .sidebar .sidebarItem > img:hover::after{
     -webkit-transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
    transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
 }
